@mixin breakpoint1 {
  @media screen and (max-width: 484px) {
    @content;
  }
}
@mixin breakpoint2 {
  @media screen and (max-width: 936px) {
    @content;
  }
}
@mixin breakpoint3 {
  @media screen and (max-width: 1055px) {
    @content;
  }
}
@mixin breakpoint4 {
  @media screen and (max-width: 645px) {
    @content;
  }
}

.App {
  overflow-x: hidden;
  background-color: #69be94;
  width: 100%;
  & header {
    height: 90px;
    border-bottom: 1px solid black;
    position: sticky;
    background-color: white;
    top: 0px;
    left: 0px;
    z-index: 1;
    & .head {
      display: flex;
      justify-content: space-between;
      max-width: 1136px;
      margin-left: auto;
      margin-right: auto;
      & > a {
        text-decoration: none;
        display: flex;
        & .logo {
          height: 80px;
          width: 80px;
          background-image: url("../public/logo.svg");
          background-position: center;
          background-size: 120px 120px;
        }
        & h1 {
          color: #40576d;
          padding-left: 20px;
          @include breakpoint1 {
            margin: 0;
            max-width: 250px;
          }
        }
      }
      & nav {
        display: flex;
        align-items: center;
      }
    }
  }
  & .wrapper {
    width: 100%;
  }
  & .container {
    max-width: 1136px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    & .banner {
      background-color: white;
      & .board {
        justify-content: space-around;
        color: black;
        display: flex;
        flex-wrap: wrap;
        @include breakpoint4 {
          width: 120px;
        }
        &__item {
          box-sizing: border-box;
          width: 180px;
          color: #69be94;
          padding: 20px;
          &--raise-awareness,
          &--informed-decisions,
          &--compare-footprints {
            &:hover {
              & .board__item__icon {
                transform: scaleX(-1);
                transition: top 0.4s, transform 0.4s;
              }
            }
          }
          &--track-progress {
            &__icon {
              background-size: 120px 120px!important;
            }
            &:hover {
              & .board__item__icon {
                transform: rotate(360deg);
                transition: top 0.4s, transform 0.4s;
              }
            }
          }
          &--take-action,
          &--environmental-contribution {
            &:hover {
              & .board__item__icon {
                top: -10px;
                transition: top 0.4s;
              }
            }
          }
          @include breakpoint3 {
            width: 120px;
          }
          &__icon {
            transform: none;
            position: relative;
            top: 0px;
            transition: top 0.4s, transform 0.4s;
            margin-left: auto;
            margin-right: auto;
            height: 100px;
            width: 100px;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            filter: invert(91%) sepia(5%) saturate(3522%) hue-rotate(85deg) brightness(85%) contrast(71%);
            @include breakpoint3 {
              width: 80px;
            }
          }
          &__title {
            width: 100%;
            margin-top: 0;
            cursor: default;
            text-align: center;
            color: #40576d;
            @include breakpoint3 {
              font-size: 1em;
            }
          }
          &--raise-awareness {
            &__icon {
              background-image: url("Media/self-awareness.png");
            }
          }
          &--informed-decisions {
            &__icon {
              background-image: url("Media/decision-making.png");
            }
          }
          &--track-progress {
            &__icon {
              background-image: url("Media/track.png");
            }
          }
          &--compare-footprints {
            &__icon {
              background-image: url("Media/benchmarking.png");
            }
          }
          &--take-action {
            &__icon {
              background-image: url("Media/actionable.png");
            }
          }
          &--environmental-contribution {
            &__icon {
              background-image: url("Media/sustainable.png");
            }
          }
        }
      }
    }
    & .separator {
      background-color: #69be94;
      max-width: 696px;
      & .facts {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        color: #40576d;
        @include breakpoint2 {
          display: block;
        }
        &__item {
          text-align: center;
          max-width: 40%;
          padding: 20px;
          @include breakpoint2 {
            padding-top: 0;
            padding-bottom: 0;
            max-width: 100%;
          }
        }
      }
      & .calculator__links {
        padding: 20px;
        &--title {
          margin: 0;
          padding-left: 50px;
          padding-right: 50px;
          color: #40576d;
        }
      }
    }
  }
  footer {
    border-top: 1px solid black;
    width: 100%;
    background-color: white;
    & .foot {
      max-width: 1136px;
      font-size: 0.7em;
      display: flex;
      justify-content: space-around;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 20px;
      &__section {
        &__title {
          color: #40576d;
          margin: 0;
          padding: 20px;
        }
        &__content {
          max-width: 300px;
          display: flex;
          flex-wrap: wrap;
          &__link {
            height: 30px;
            flex: 1 1 100px;
            margin: 0;
            padding-left: 20px;
            padding-right: 20px;
          }
        }
      }
    }
  }
  .navigators {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    &__link {
      margin: 5px;
      width: 50%;
      padding: 10px;
      text-align: center;
      border-radius: 10px;
      width: 100px;
      transition: background-color 0.4s, color 0.4s;
      &:hover {
        cursor: pointer;
        transition: background-color 0.4s, color 0.4s;
      }
      &--white {
        background-color: white;
        color: #40576d;
        border: 1px solid #40576d;
        &:hover {
          background-color: #40576d;
          color: white;
        }
      }
      &--green {
        background-color: #69be94;
        color: white;
        border: 1px solid #40576d;
        &:hover {
          background-color: #40576d;
          color: white;
        }
      }
      & a {
        color: #40576d;
        text-decoration: none!important;
        &:hover {
          color: #69be94;
        }
      }
    }
  }
}
